const validate = (values) => {
  let errors = {};
  if (!values.name){
    errors.name = 'El nombre es requerido';
  } else if (!values.email) {
    errors.email = 'El email es requerido';
  } else if (!/\S+@\S+\.\S+/.test(values.email)) {
    errors.email = 'El email no es vlido';
  } else if (!values.phone) {
    errors.phone = 'El Telfono es requerido';
  } else if (!/^(\+34|0034|34)?[6789]\d{8}$/.test(values.phone.replace(/\s/g, ''))) {
    errors.phone = 'El nmero telefnico no es vlido';
  } else if (!values.message) {
    errors.message = 'El mensaje est vaco';
  }
  return errors;
};

